body {
  background: #FFF;
  font-family: "MuseoSansCyrillic", Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 300;
  line-height: 1.73;
  line-height: 1.4;
  color: #363636;
}

.btn {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  font-family: "MuseoSansCyrillic", Helvetica, sans-serif;
  font-size: 14px;
  text-align: center;
  line-height: 2.36;
  padding: 12px 39px;
  background-color: #db019d;
  color: #fff;
  display: inline-block;
}

@media only screen and (max-width: 599px) {
  .btn {
    padding: 12px 15px;
  }
}

.btn_wide {
  width: 100%;
  padding: 12px 0;
}

.btn_big {
  padding: 12px 75px;
}

.title {
  position: relative;
  font-size: 40px;
  font-weight: 900;
  line-height: 1;
  padding-bottom: 22px;
}

.title:after {
  content: "";
  background-color: #db019d;
  position: absolute;
  left: 5px;
  bottom: 0;
  width: 213px;
  height: 5px;
}

.title_sm:after {
  width: 102px;
}

.title-small {
  font-size: 18px;
  font-weight: 900;
  line-height: 1;
}

.inner {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.row {
  margin: 0 -15px;
  display: flex;
  flex-wrap: wrap;
}

.col {
