.block {
  display: flex;
}

.header {
  background-color: transparent;
  position: fixed;
  z-index: 5;
  color: #fff;
  width: 100%;
  padding: 13px 0 11px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.header a {
  color: #fff;
  text-decoration: none;
}

.header__logo {
  font-family: "Bearpaw", Helvetica, sans-serif;
  font-size: 42px;
  float: left;
}

.header__nav {
  float: right;
  margin-top: 20px;
  margin-right: 80px;
}

@media only screen and (max-width: 1259px) {
  .header__nav {
    margin-right: 20px;
  }
}

.header__links {
  float: right;
  margin-top: 20px;
  font-size: 17px;
  display: flex;
  align-items: center;
}

.header__links .icon {
  display: block;
  font-size: 20px;
}

.header__tel {
  display: block;
  line-height: 1;
}

.header__inst {
  display: block;
  position: relative;
  margin-left: 30px;
  line-height: 1;
}

@media only screen and (max-width: 1259px) {
  .header__inst {
    margin-left: 15px;
  }
}

.header__inst:after {
  content: "";
  position: absolute;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav__list {
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.main {
  height: 100vh;
  min-height: 630px;
  background-color: rgba(43, 46, 38, 0.5);
}

.main .error {
  color: #fff;
}

.main__video {
  position: relative;
  height: 100%;
  width: 100%;
}

.main__video:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  content: "";
  z-index: 1;
  background-color: rgba(17, 13, 127, 0.45);
}

.main__container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 4;
  color: #fff;
  padding: 85px 0;
  text-align: center;
  font-size: 0;
}

.main__container:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.main__content {
  display: inline-block;
  vertical-align: middle;
  padding: 150px 15px;
  max-width: 920px;
}

.main__footer {
  position: absolute;
  font-family: "Roboto", Helvetica, sans-serif;
  font-size: 14px;
  width: 100%;
  bottom: 0;
  margin-bottom: 46px;
}

.main__footer-wrap {
  display: flex;
  justify-content: space-between;
}

.main__title {
  font-weight: 900;
  font-size: 50px;
  text-transform: uppercase;
}

.main__subtitle {
  display: inline-block;
  font-size: 17px;
  font-weight: 500;
  max-width: 820px;
  margin-top: 13px;
}

.main__form {
  max-width: 865px;
  margin: 0 auto;
  margin-top: 60px;
}

.benefits-item {
  display: flex;
  align-items: center;
  text-align: left;
}

.benefits-item__icon {
  font-size: 32px;
  color: #fff;
  margin-right: 16px;
}

.benefits-item__icon .icon {
  display: block;
}

.benefits-item__text {
  max-width: 170px;
}

.form__input {
  width: 100%;
  font-family: "MuseoSansCyrillic", Helvetica, sans-serif;
  line-height: 2.2;
  padding: 13px 24px;
  border: 1px solid #989898;
  color: #666696;
}

.form__input::placeholder {
  color: #666696;
}

.for-whom {
  padding-top: 65px;
  position: relative;
  z-index: 1;
}

.for-whom__title {
  margin-bottom: 45px;
}

.for-whom__text {
  line-height: 2.2;
}

.for-whom__item {
  max-width: 665px;
}

.for-whom__item:not(:last-child) {
  margin-bottom: 47px;
}

.for-whom__topic {
  margin-bottom: 10px;
}

.for-whom__img {
  position: absolute;
  top: 137px;
  right: 0;
  z-index: -1;
}

.for-whom__img img {
  display: block;
  max-width: 100%;
}

.video {
  padding-top: 60px;
}

.video__title {
  margin-bottom: 63px;
}

.about {
  padding-top: 70px;
}

.about__title {
  margin-bottom: 47px;
}

.about__content {
  padding: 38px 0 46px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.about__text {
  padding: 50px 35px 30px;
  max-width: 680px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

@media only screen and (max-width: 767px) {
  .about__text {
    padding: 20px;
  }
}

.about__item:not(:last-child) {
  margin-bottom: 22px;
}

.about__topic {
  margin-bottom: 14px;
}

.about__list-item {
  position: relative;
  padding-left: 10px;
}

.about__list-item:before {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #363636;
  left: 0;
  top: 8px;
  border-radius: 50%;
}

.price {
  padding: 50px 0 40px;
  background-color: #f6f7fd;
}

.price__title {
  margin-bottom: 50px;
}

.price__note {
  color: #666696;
  font-size: 16px;
  margin-top: 36px;
}

.price__note span {
  color: #db019d;
}

@media only screen and (max-width: 1023px) {
  .price .col:not(:last-child) {
    margin-bottom: 20px;
  }
}

.price-item {
  padding: 35px 43px 42px;
  text-align: center;
  box-shadow: 0 0 15px rgba(184, 184, 184, 0.48);
  background-color: #fff;
}

@media only screen and (max-width: 599px) {
  .price-item {
    padding: 15px;
  }
}

.price-item:last-child {
  margin-bottom: 0;
}

.price-item__subtitle {
  position: relative;
  margin: 5px 0 25px;
  font-size: 14px;
  line-height: 1.57;
  min-height: 65px;
}

.price-item__subtitle:after {
  content: "";
  position: absolute;
  background-color: #db019d;
  box-shadow: 0px 0px 22px 0px rgba(184, 184, 184, 0.48);
  position: absolute;
  width: 234px;
  height: 3px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.price-item__cost {
  position: relative;
  margin-bottom: 32px;
}

.price-item__value {
  display: block;
  margin-top: 5px;
  font-size: 50px;
  font-weight: 500;
  line-height: 1.12;
}

.price-item__hint {
  position: absolute;
  left: 0;
  bottom: -24;
  width: 100%;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
}

@media only screen and (max-width: 767px) {
  .price-item__hint {
    font-size: 14px;
  }
}

.price-item__hint span {
  color: #db019d;
}

.price-item__time {
  font-size: 25px;
  margin-bottom: 25px;
}

.students {
  padding: 60px 0 70px;
}

.students__title {
  margin-bottom: 50px;
}

.students__list {
  box-shadow: 0 0 15px rgba(184, 184, 184, 0.48);
}

.students-item {
  padding: 35px 60px 40px;
  text-align: center;
  font-size: 14px;
  line-height: 2.357;
}

.students-item__title {
  margin-bottom: 10px;
}

.students-item__subtitle {
  margin-bottom: 15px;
}

.students-item__img {
  width: 166px;
  height: 166px;
  border-radius: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: inline-block;
  vertical-align: top;
  border: 1px solid #fff;
  box-shadow: 0 0 22px rgba(184, 184, 184, 0.48);
  margin-bottom: 35px;
}
